// get all page stuff

const items = document.querySelectorAll(".scroll-text-box");
const images = document.querySelectorAll(".case_item__image");
const scrolltabs = document.querySelectorAll(".scroll--menu_item");
let tabArray = Array.from(scrolltabs);
let imagesArray = Array.from(images);



