// doesn't work iwth LOCOMOTIVE SCROLL

document.addEventListener("scroll", function () {
    // do this for each individual version of the block on the page

    items.forEach((section) => {
        // when section is in viewport

        if (isInViewport(section)) {
            //  go through image array and show corresponding image when text is showing

            // gsap 

            gsap.to(section.querySelector('.case__title h2'), {
                scrollTrigger: {
                    trigger: section,
                    start: "top center",
                    once: true
                },
                opacity: 1,
                y: 0,
                duration: 1,
                ease: "Power4.easeOut.easeOut",

            })

            gsap.to(section.querySelector('.case__desc p'), {
                scrollTrigger: {
                    trigger: section,
                    start: "top center",
                    once: true
                },
                opacity: 1,
                y: 0,
                duration: 1,
                ease: "Power4.easeOut.easeOut",
                stagger: 0.25
            })

            for (var i = 0; i < imagesArray.length; i++) {
                let dataImage = imagesArray[i].getAttribute("data-image");
                let dataSection = section.getAttribute("data-section");



                imagesArray[i].classList.remove("js-item_visible");
                tabArray[i].classList.remove("js-tab-visible");

                if (dataImage == dataSection) {
                    imagesArray[i].classList.add("js-item_visible");
                    tabArray[i].classList.add("js-tab-visible");
                }
            }
        }
    });
});